.root {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}

.root input {
    width: 100%;
    font-size: 20px;
    padding: 0px 15px;
    border: none;
    outline: none;
    border-radius: 5px 0px 0px 5px;
}

.root input::placeholder {
    color: var(--light-grey);
}